import React from 'react';
import s from './Portfolio-Item.scss';

export const PortfolioItem = React.createClass({
  render() {
    return (
      <div className={s.card}>
        <img className={s['card-img']} alt="portfolio" src={this.props.image} />
      </div>
    );
  }
});



