import React from 'react';
import s from './Portfolio-Carousel.scss';
import Slider from 'react-slick';

export const PortfolioCarousel = React.createClass({
  render: function () {
    var settings = {
      dots: true,
      infinite: false,
      speed: 500,
      arrows: false,
      slidesToShow: 3,
      slidesToScroll: 3,
      responsive: [ 
        { breakpoint: 554, settings: { slidesToShow: 1, slidesToScroll: 1  }  } 
      ]
    };

    const portfolioItems = this.props.portfolio.map(portfolioItem => 
      <div><div className={s['portfolio-item']}>{portfolioItem}</div></div>
    );

    return (
      <Slider {...settings}>
        {portfolioItems}
      </Slider>
    );
  }
});



