import React from 'react';
import { Route, IndexRoute } from 'react-router';

//import { App, Home, NotFound } from './components/App.js';
import { App, NotFound } from './components/App.js';
import { Home } from './components/Home/Home.js';
import { About } from './components/About/About.js';

export const routes = (
  <Route path='/' title='Eric Koh – Web Design & Front-end Development – Shanghai, China' component={App}>
    <IndexRoute title='Eric Koh – Web Design & Front-end Development – Shanghai, China' component={Home} />
    {/*<Route path='about' title='App - About' component={About} />*/}
    <Route path='*' title='404: Not Found' component={NotFound} />
  </Route>
);

export default routes;



