import React from 'react';
import s from './Footer.scss';
import icons from '../../assets/styles/_icons.scss';

export const Footer = React.createClass({
  render() {
    return (
      <footer className={s.section}>
        <div className={s.wrapper}>
          <div className={s.inner}>
            <div className={s.content}>Designed and built with <a className={s.link}><i className={icons.icon + ' ' + icons['icon-heart'] + ' ' + s.heart}></i></a> by Eric Koh.</div>
            <div className={s.group}>
              <a className={s.link + ' ' + s.github} href='https://github.com/iEricKoh' target='_blank'><i className={icons.icon + ' ' + icons['icon-github'] + ' ' + s.icon}></i></a>
              <a className={s.link + ' ' + s.linkedin} href='https://cn.linkedin.com/in/ierickoh' target='_blank'><i className={icons.icon + ' ' + icons['icon-linkedin'] + ' ' + s.icon}></i></a>
              <a className={s.link + ' ' + s.dribbble} href='https://dribbble.com/EricKoh' target='_blank'><i className={icons.icon + ' ' + icons['icon-dribbble'] + ' ' + s.icon}></i></a>
              <a className={s.link + ' ' + s.instagram} href='https://www.instagram.com/ierickoh/' target='_blank'><i className={icons.icon + ' ' + icons['icon-instagram'] + ' ' + s.icon}></i></a>
            </div>
          </div>
        </div>
      </footer>
    )
  }
});



