import React from 'react';
import s from './Contact.scss';
import icons from '../../assets/styles/_icons.scss';

export const Contact = React.createClass({
  render() {
    return (
      <section className={s.section}>
        <div className={s.inner}>
          {/*<div className={s.heading}>Have a cool project in mind, and think I can help you with it?</div>*/}
          <div>
            <div className={s.heading}>Ready to start?</div>
            <div className={s.content}>Have a cool project in mind, and think I can help you with it?</div>
            <div className={s.content}>I am always looking for exciting projects to work on.</div>
          </div>
          <div>
            <a className={s.btn} href='mailto:EricKoh.dev@gmail.com?subject=Hi Eric Koh'><i className={icons.icon + ' ' + icons['icon-paper-plane']}></i> LET'S TALK</a>
          </div>
          {/*<div className={s.content}>I am currently available for freelance work on smaller projects.</div>*/}
          {/*<div className={s.content}>Send me a message. <a className={s.link} href="mailto:EricKoh.dev@gmail.com">EricKoh.dev@gmail.com</a></div>*/}
        </div>
      </section>
    )
  }
});



