import React from 'react';
import s from './Intro.scss';

export const Intro = React.createClass({
  render() {
    return (
      <section className={s.section}>
        <div className={s.heading}>About Me</div>
        <p className={s.content}>I am Eric Koh, a <em>web designer</em> and <em>front-end developer</em> who lives and works in Shanghai, China.</p>
        <p className={s.content}>I am focusing and working with HTML, CSS and JavaScript. My expertise also includes UX/UI design, interaction design, product strategy, qualitative/quantitative user research.</p>

        <strong className={s['sub-heading']}>Being a developer</strong>
        <p className={s.content}>As a self-motivated web developer, I am always interested in trying new things. I really love diving into new problems and solving them. I believe that the key to progress is trying new think. Programming allows me to constantly explore new things and challenge myself. </p>

        <strong className={s['sub-heading']}>Being a designer</strong>
        <p className={s.content}>As a designer, I can see the things that other people don't. My sensibilities, as well as my sensitivities, give me great pattern-recognition skills. I have a passion for clean, pixel perfect design and a relentless attention to detail.</p>

      </section>
    )
  }
});



