import React from 'react';
import s from './Portfolio.scss';
import { PortfolioItem } from '../Portfolio-Item/Portfolio-Item.js';
import { PortfolioCarousel } from '../Portfolio-Carousel/Portfolio-Carousel.js';

import portfolio1 from '../../assets/images/portfolio1.jpg';
import portfolio2 from '../../assets/images/portfolio2.jpg';
import portfolio3 from '../../assets/images/portfolio3.jpg';

const portfolio = [<PortfolioItem image={portfolio1} />, <PortfolioItem image={portfolio2} />, <PortfolioItem image={portfolio3} />];

export const Portfolio = React.createClass({
  render() {
    return (
      <section className={s.section}>
        <div className={s.inner}>
          <div className={s.heading}>PORTFOLIO</div>
          <div className={s['sub-heading']}>Check out some of my recent projects.</div>
          <PortfolioCarousel portfolio={portfolio} />
        </div>
      </section>
    )
  }
});



